body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

@media screen and (min-width: 679px) {
  .hide-for-desktop {
    display: none !important;
  }
}

@media screen and (max-width: 679px) {
  .hide-for-mobile {
    display: none !important;
  }
}

.dangerously-override-border {
  box-shadow: 0 1px 3px 0 white, 0 0 0 1px white !important;
}

